export const GamesAnnouncementScreen = () => {
    return (
      <div className="games-announcement-screen"> {/*   */}
        <h1> </h1>
        <GameSlider />
      </div>
    );
  };


export const GameSlider = () => {
  //      
  const games = [
    {
        "date": "21 .",
        "tournament": " ",
        "teams": " \"\" vs  \"\"",
        "link": "/game/1"
    },
    {
        "date": "28 .",
        "tournament": " ",
        "teams": " \"\" vs  \"\"",
        "link": "/game/2"
    },
    {
        "date": "05 .",
        "tournament": " ",
        "teams": " \"\" vs  \"\"",
        "link": "/game/3"
    },
    {
        "date": "11 .",
        "tournament": " ",
        "teams": " \"\" vs  \"\"",
        "link": "/game/4"
    },
    {
        "date": "19 .",
        "tournament": " ",
        "teams": " \"\" vs  \"\"",
        "link": "/game/5"
    },
    {
        "date": "25 .",
        "tournament": " ",
        "teams": " \"\" vs  \"\"",
        "link": "/game/6"
    },
    {
        "date": "17 .",
        "tournament": " ",
        "teams": " \" \" vs  \"\"",
        "link": "/game/7"
    },
    {
        "date": "24 .",
        "tournament": " ",
        "teams": " \"\" vs \" \"",
        "link": "/game/8"
    },
    {
        "date": "31 .",
        "tournament": " ",
        "teams": " \"\" vs  \"\"",
        "link": "/game/9"
    }
]

  const [startIndex, setStartIndex] = useState(0);
  const visibleCards = 3; //   
  const cardWidth = 250; // ,    250px
  const gap = 20; //   

  const handlePrev = () => {
    setStartIndex((prevIndex) => (prevIndex > 0 ? prevIndex - 1 : 0));
  };

  const handleNext = () => {
    setStartIndex((prevIndex) => (prevIndex < games.length - visibleCards ? prevIndex + 1 : prevIndex));
  };

  const offset = -(startIndex * (cardWidth + gap));

  return (
    <div className="slider-container">
      <button onClick={handlePrev}></button>
      <div className="cards-wrapper">
        <div className="cards-container" style={{ transform: `translateX(${offset}px)` }}>
          {games.map((game, index) => (
            <GameCard key={index} game={game} />
          ))}
        </div>
      </div>
      <button onClick={handleNext}></button>
    </div>
  );
};


export const GameCard = ({ game }) => {
  return (
    <div className="game-card">
      <div className="game-date-time">
        {game.date}  {game.time}
      </div>
      <div className="game-tournament">{game.tournament}</div>
      <div className="game-teams">{game.teams}</div>
      <a href={game.link} className="game-link">  </a>
    </div>
  );
};
